import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import {
  ShowModalJenis,
  showModal,
  getDataJenis,
  hideModal,
} from "../../../actions/datamaster_action.jsx";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import {
  NotifSucces,
  ToastNotification,
} from "../../../components/helpers/notification.jsx";
import { reset } from "redux-form";
import { deleteData, postData, putData } from "../../../config/axios.jsx";
import { ErrorHandling } from "../../../components/helpers/function.jsx";
import Tabel from "../../../components/helpers/tabel.jsx";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";
import FormModalDataJenis from "./FormModalDataJenis.jsx";
const maptostate = (state) => {
  return {
    DataJenis: state.datamaster.getDataJenis,
    hideModal: state.datamaster.modalDialog,
  };
};

const hapusDataJenis = (kode,params, dispatch) => {
  Swal.fire({
    // title: 'Anda Yakin !!',
    // text: 'Ingin Menghapus Data Ini ?',
    html:
      "Apakah Anda Yakin Ingin " +
      "Menghapus " +
      "<h1><b>Kode Jenis " +
      kode +
      "</b> ?</h1>",
    icon: "warning",
    position: "top-center",
    cancelButtonText: "Tidak",
    showCancelButton: true,
    confirmButtonText: "OK",
    showConfirmButton: true,
  }).then((result) => {
    if (result.isConfirmed) {
      deleteData("jenis/delete/by-kode-jenis/" + params, {
        data: {
          kode_group: params,
        },
      }).then(() => {
        NotifSucces("Data Berhasil Di Hapus").then(() =>
          dispatch(getDataJenis())
        );
      }).catch((err) => {
        ToastNotification("error", err.response.data)
      })
    }
  });
};
class MasterJenis extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      columns: [
        {
          dataField: "kode_group",
          text: "Kode Group",
        },
        {
          dataField: "kode_dept",
          text: "Kode Dept ",
          sort: true,
        },
        {
          dataField: "nama_dept",
          text: "Nama Dept",
        },

        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            let dataEdit = {
              id: row._id,
              kode_dept: row.kode_dept,
              nama_dept: row.nama_dept,
              kode_group: row.kode_group,
            };
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => this.fomrEditDataJenis(dataEdit)}
                    className="btn btn-primary mr-3"
                  >
                    {" "}
                    <i className="fa fa-edit"></i>
                  </button>
                  <button
                    onClick={() =>
                      hapusDataJenis(row.kode_dept,row._id, this.props.dispatch)
                    }
                    className="btn btn-danger"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    this.props.dispatch(getDataJenis());
  }
  handleSubmit(data) {
    this.setState({ isLoading: true });
    // console.log(data);
    if (data.kode_dept === undefined) {
      Swal.fire({
        title: "Oopss!! ",
        text: "Kode Group Harus Diisi !!!",
        icon: "info",
      });
      this.setState({ isLoading: false });
      return false;
    }
    let data_edit = {
      nama_dept: data.nama_dept,
    };
    let data_post = {
      kode_dept: data.kode_dept,
      nama_dept: data.nama_dept,
      kode_group: data.kode_group,
    };
    console.log(data);
    console.log(data.id);
    this.state.isEdit
      ? putData("jenis/edit/by-kode-jenis/" + data.id, data_edit)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => {
            ToastNotification("success", "Data Berhasil Diedit");
          })
          .then(() => {
            this.props.dispatch(getDataJenis());
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .then(() => {
            this.props.dispatch(reset("ModalDataJenis"));
          })
        .catch((error) => {
          ToastNotification("info", error.response === undefined ? "Terjadi Kesalahan Saat Megirim Data " : error.response.data)
          this.setState({ isLoading: false });
        })
      : postData("jenis/add", data_post)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => {
            ToastNotification("success", "Data Berhasil Disimpan");
          })
          .then(() => {
            this.props.dispatch(getDataJenis());
          })
          .then(() => {
            this.props.dispatch(reset("ModalDataJenis"));
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .catch((error) =>
            ErrorHandling(
              error,
              "jenis/reactive/by-kode-jenis/",
              // data.kode_dept,
              data_post,
              this.props.dispatch,
              getDataJenis(),
              hideModal(),
              "ModalDataJenis"
            ).then(() => {
              this.setState({ isLoading: false });
            })
          );
  }

  fomrEditDataJenis(dataEdit) {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalJenis(dataEdit));
    this.setState({
      isEdit: true,
    });
  }
  ShowModalJenis() {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalJenis(false));
    this.setState({
      isEdit: false,
    });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Data Master</Link>
          </li>
          <li className="breadcrumb-item active">Master Jenis</li>
        </ol>
        <h1 className="page-header">Master Jenis </h1>
        <Panel>
          <PanelHeader>Master Jenis</PanelHeader>
          <br />

          {/* Master Jenis */}
          <div className="container">
            <Tabel
              keyField="kode_dept"
              tambahData={true}
              handleClick={() => this.ShowModalJenis()}
              exportCsv={true}
              data={this.props.DataJenis}
              columns={this.state.columns}
              empty={this.props.DataJenis}
              textEmpty="Data Jenis Kosong"
            />
          </div>
          <br />
          {/* End Master Jenis */}
          <ModalGlobal
            size="P"
            title={this.state.isEdit ? "Edit Data Jenis" : "Tambah Data Jenis"}
            content={
              <FormModalDataJenis
                isLoading={this.state.isLoading}
                isEdit={this.state.isEdit}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            }
          />

          {/* End Tambah Master Jenis  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(MasterJenis);
