import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { hideModal } from "../../../actions/datamaster_action";

import { HiidenFiled, ReanderField, ReanderSelect } from "../../../components/helpers/field";
import { getData } from "../../../config/axios";
import ValidasiMasterJenis from "../../../Validasi/ValidasiMasterJenis.jsx";
// import Loading from 'react-fullscreen-loading';

const maptostate = (state) => {
  if (state.datamaster.ShowModalJenis !== null) {
    return {
      initialValues: {
        // DataGroup: state.datamaster.GetDataGroup,
        id: state.datamaster.ShowModalJenis.id,
        kode_group: state.datamaster.ShowModalJenis.kode_group,
        kode_dept: state.datamaster.ShowModalJenis.kode_dept,
        nama_dept: state.datamaster.ShowModalJenis.nama_dept,
      },
    };
  }
};
class FormModalDataJenis extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listgroup: [],
      aktif: false
    };
  }
  componentDidMount() {
    getData("group/get/all")
      .then((res) => {
        this.setState({
          listgroup: res.data,
        });
      })
      .catch((err) => {
        console.log(err);
      });
    setTimeout(() => {
      if (this.props.isEdit) {
        document.getElementById('nama_dept').focus()
      } else {
        document.getElementById('kode_group').focus()
      }
    }, 100);
   
  }
  setIndex() {
    document.getElementById('kode_dept').focus()
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        {
          this.props.isEdit ? <>
             <Field
              name="id"
              component={HiidenFiled}
              type="hidden"
              readOnly={this.props.isEdit}
            />
          </> : null
        }
        <div className="form-group">
          <Field
            onChange={()=>this.setIndex()}
            tabIndex="1"
            id="kode_group"
            label="Kode Group"
            name="kode_group"
            options={this.state.listgroup
              .filter((list) => list.kode_group !== undefined)
              .map((list) => {
                let data = {
                  value: list.kode_group,
                  name: list.kode_group + " - " + list.nama_group,
                };
                return data;
              })}
            value={this.props.kode_group}
            disabled={this.props.isEdit}
            placeholder={"Silahkan Pilih Kode Group"}
            component={ReanderSelect}
          />
        </div>
        <Field
          id="kode_dept"
          tabIndex="2"
          name="kode_dept"
          component={ReanderField}
          type="text"
          label="Kode Jenis"
          readOnly={this.props.isEdit}
          placeholder="Masukan Kode Jenis"
        />
        <Field
          tabIndex="3"
          id="nama_dept"
          name="nama_dept"
          component={ReanderField}
          type="text"
          label="Nama Jenis"
          onBlur={() => this.setState({ aktif: true })}
          placeholder="Masukan Nama Jenis"
        />

        <div className="row">
          <div className="col-12 text-right">
            <button
              className="btn btn-white"
              onClick={() => this.props.dispatch(hideModal())}
              type="button"
              disabled={this.props.isLoading}
            >
              Batal
            </button>
            &nbsp;
            <button
              tabIndex="3"
              className={
                this.state.aktif ? "btn btn-primary" : "btn btn-white"
              }
              disabled={this.props.submitting}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </div>
        </div>
         {/* {/* {this.state.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null} */}
      </form>
    );
  }
}

FormModalDataJenis = reduxForm({
  form: "ModalDataJenis",
  enableReinitialize: true,
  validate: ValidasiMasterJenis,
})(FormModalDataJenis);
export default connect(maptostate, null)(FormModalDataJenis);
